@font-face{font-family:quickSand_light;src:url("/assets/fonts/Quicksand-Light.ttf")}@font-face{font-family:quickSand_medium;src:url("/assets/fonts/Quicksand-Medium.ttf")}.App{text-align:center}body{background-color:#eee;font-family:quickSand_light}button{color:black;background-color:#ffffff;border-radius:3px;border-width:2px;padding-top:6px;padding-bottom:6px;padding-right:12px;padding-left:12px}button:disabled,button[disabled]{border:1px solid #999999;background-color:#cccccc;color:#666666}.divider{width:10px;height:auto;display:inline-block}form{margin-top:10px;margin-bottom:10px}input{margin-left:10px;margin-right:10px}

.header-logo{width:100px;margin-left:50px}.nav-link{font-family:quickSand_medium;font-size:x-large;letter-spacing:2px;padding:0 30px !important}.dropdown-menu{background:none}.dropdown-item{text-align:center;color:#333 !important}.dropdown-item.active{background:none}.dropdown-item:hover{background:#e6e6e6;transition:.5s;color:#8787fe !important}.dropdown-toggle{transition:.5s}.dropdown-toggle:after{content:none !important}.dropdown-toggle:hover{transition:.2s;background:#51a6fc;background:linear-gradient(149deg, #51a6fc 0%, #9537ff 100%);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.burgerContainer{padding:0 50px}.burgerContainer:hover{cursor:pointer}.burgerIcon{background:#51a6fc;background:linear-gradient(149deg, #51a6fc 0%, #9537ff 100%)}.burgerContainer:hover .burgerIcon:nth-of-type(1){transform:translateY(11px) rotate(-45deg);transition:.5s;background:#51a6fc;background:linear-gradient(149deg, #51a6fc 0%, #9537ff 100%)}.burgerContainer:hover .burgerIcon:nth-of-type(2){opacity:0;transform:scale(0.5) rotate(360deg);transition:.5s}.burgerContainer:hover .burgerIcon:nth-of-type(3){transform:translateY(-11px) rotate(45deg);transition:.5s;background:#51a6fc;background:linear-gradient(149deg, #51a6fc 0%, #9537ff 100%)}.burgerIcon{width:35px;height:5px;background-color:#444;margin:6px 0;border-radius:50px;transition:.5s}.burgerIcon:nth-of-type(1){margin-left:4px}.burgerIcon:nth-of-type(2){width:43px}.burgerIcon:nth-of-type(3){margin-left:4px}

.heading{text-align:center;margin-top:200px;font-size:52px}.background{position:fixed;left:0;bottom:0;opacity:10}.background img{position:fixed;bottom:0}.background img:nth-child(1){position:fixed;-webkit-animation:hover 6s infinite ease-in-out;animation:hover 6s infinite ease-in-out}.background img:nth-child(2){position:fixed;-webkit-animation:hover 5s infinite ease-in-out;animation:hover 5s infinite ease-in-out}.background img:nth-child(3){position:fixed;-webkit-animation:hover 4s infinite ease-in-out;animation:hover 4s infinite ease-in-out}@-webkit-keyframes hover{0%{transform:translateY(0px)}50%{transform:translateY(20px)}100%{transform:translateY(0px)}}@keyframes hover{0%{transform:translateY(0px)}50%{transform:translateY(20px)}100%{transform:translateY(0px)}}

.node circle{fill:#fff;stroke:black;stroke-width:1px}.node .current-node-of-interest{fill:blue;-webkit-animation:colorin-current-node-of-interest 0.5s;animation:colorin-current-node-of-interest 0.5s}@-webkit-keyframes colorin-current-node-of-interest{from{fill:white}to{fill:blue}}@keyframes colorin-current-node-of-interest{from{fill:white}to{fill:blue}}.node .child-to-current{fill:blue;-webkit-animation:colorin-child-to-current 0.5s;animation:colorin-child-to-current 0.5s}@-webkit-keyframes colorin-child-to-current{from{fill:#75dfff}to{fill:blue}}@keyframes colorin-child-to-current{from{fill:#75dfff}to{fill:blue}}.node .current-neighbor-of-interest{fill:#75dfff;-webkit-animation:colorin-current-neighbor-of-interest 0.5s;animation:colorin-current-neighbor-of-interest 0.5s}@-webkit-keyframes colorin-current-neighbor-of-interest{from{fill:white}to{fill:#75dfff}}@keyframes colorin-current-neighbor-of-interest{from{fill:white}to{fill:#75dfff}}.node .node-complete-directed{fill:green;-webkit-animation:done-node 0.5s;animation:done-node 0.5s}@-webkit-keyframes done-node{from{fill:#75dfff}to{fill:green}}@keyframes done-node{from{fill:#75dfff}to{fill:green}}.node text{font:16px sans-serif}.node .node-complete-tree{fill:green;-webkit-animation:color-in-visited-node-tree 1s;animation:color-in-visited-node-tree 1s}@-webkit-keyframes color-in-visited-node-tree{from{fill:#fff}to{fill:green}}@keyframes color-in-visited-node-tree{from{fill:#fff}to{fill:green}}.node .node-complete-undirected{fill:green;-webkit-animation:node-visited 1s;animation:node-visited 1s}@-webkit-keyframes node-visited{from{fill:blue}to{fill:green}}@keyframes node-visited{from{fill:blue}to{fill:green}}.node .child-node{fill:#fff;-webkit-animation:child-node 2s;animation:child-node 2s}@-webkit-keyframes child-node{0%,100%{fill:#fff}30%{fill:#5b9cfc}}@keyframes child-node{0%,100%{fill:#fff}30%{fill:#5b9cfc}}.node .parent-node{fill:#fff;-webkit-animation:parent-node 2s;animation:parent-node 2s}@-webkit-keyframes parent-node{0%,100%{fill:#fff}30%{fill:red}}@keyframes parent-node{0%,100%{fill:#fff}30%{fill:red}}.node .left-right-child-node{fill:#03fca5;-webkit-animation:left-right-child-node 1s;animation:left-right-child-node 1s}@-webkit-keyframes left-right-child-node{from{fill:#fff}to{fill:#03fca5}}@keyframes left-right-child-node{from{fill:#fff}to{fill:#03fca5}}

.link{fill:none;stroke:#ccc;stroke-width:2px}.link-traversed{fill:none;stroke:#ff0000;stroke-width:4px;-webkit-animation:fade-in-active-link 1s;animation:fade-in-active-link 1s}@-webkit-keyframes fade-in-active-link{from{stroke:#ccc;stroke-width:2px}to{stroke:red;stroke-width:4px}}@keyframes fade-in-active-link{from{stroke:#ccc;stroke-width:2px}to{stroke:red;stroke-width:4px}}.fade-out-link{-webkit-animation:fadeout 0.5s ease-in;animation:fadeout 0.5s ease-in;fill:none;stroke:rgba(50,50,50,0.2);stroke-width:1px}@-webkit-keyframes fadeout{from{stroke:#ff0000;stroke-width:4px}to{stroke:rgba(50,50,50,0.2);stroke-width:1px}}@keyframes fadeout{from{stroke:#ff0000;stroke-width:4px}to{stroke:rgba(50,50,50,0.2);stroke-width:1px}}.link-of-interest-ts{stroke:blue;-webkit-animation:fadein-link-ts 0.5s;animation:fadein-link-ts 0.5s}@-webkit-keyframes fadein-link-ts{from{stroke:#ccc}to{stroke:blue}}@keyframes fadein-link-ts{from{stroke:#ccc}to{stroke:blue}}.heap-link{fill:none}.fade-in-out-link{stroke-width:4px;stroke:rgba(50,50,50,0.2);-webkit-animation:fade-in-out-link 2s;animation:fade-in-out-link 2s}@-webkit-keyframes fade-in-out-link{0%,100%{stroke-width:2px;stroke:rgba(50,50,50,0.2)}30%{stroke:#5b9cfc;stroke-width:4px}}@keyframes fade-in-out-link{0%,100%{stroke-width:2px;stroke:rgba(50,50,50,0.2)}30%{stroke:#5b9cfc;stroke-width:4px}}

.graph{position:absolute}svg{overflow:visible}#graph-svg{z-index:-1}.graph-button{z-index:10}

table{border-collapse:collapse;text-align:center}td{border:1px solid black}.undirected-graph-table{margin-top:-100px}

.centroid{fill-opacity:0;r:10;-webkit-animation:centroid-animation;animation:centroid-animation;-webkit-animation-duration:2s;animation-duration:2s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-direction:alternate;animation-direction:alternate;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out}@-webkit-keyframes centroid-animation{from{r:5;opacity:0.2}to{r:30;opacity:1}}@keyframes centroid-animation{from{r:5;opacity:0.2}to{r:30;opacity:1}}.cluster-unassigned{fill:"black";stroke-width:3;stroke:"black"}.cluster0{fill:#f69;stroke-width:3;stroke:#f69}.cluster1{fill:#0c6;stroke-width:3;stroke:#0c6}.cluster2{fill:#06c;stroke-width:3;stroke:#06c}.cluster3{fill:#fc0;stroke-width:3;stroke:#fc0}.cluster4{fill:#2be3f0;stroke-width:3;stroke:#2be3f0}.cluster5{fill:#fa3b3b;stroke-width:3;stroke:#fa3b3b}.cluster6{fill:#ffa339;stroke-width:3;stroke:#ffa339}.cluster7{fill:#90c;stroke-width:3;stroke:#90c}

.active-code-line{background-color:#e8448e5c;border-radius:2px}

.sidebar{height:80px;display:flex;justify-content:center;align-items:center}.menu-bars{margin-left:2rem;font-size:2rem;background:white}.side-menu{overflow-y:scroll;max-height:50vh;background-color:whitesmoke;width:100%;display:flex;justify-content:center;right:0;transition:550ms;opacity:0;visibility:hidden;padding:10px;margin:20px}.side-menu.active{opacity:1;transition:550ms;visibility:visible;border:#ecb5cc 4px solid;border-radius:10px}.side-menu-items{width:100%;list-style:none;padding-left:5px;color:#e83e8c;font-size:large;font-family:SFMono-Regular, Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace}.side-menu-items li{overflow:hidden;white-space:nowrap;text-overflow:ellipsis}

